// MODAL :: START

.c-modal {
  &#requestDemo {
    max-width: 500px;
    border-radius: 16px;
    box-shadow: 0 30px 100px 0 rgba(135, 119, 217, 0.35);
    background-color: #ffffff;

    .mfp-close {
      opacity: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 20px;
      right: 20px;
      width: 30px;
      height: 30px;
      line-height: 30px;
      color: #5153DC;
      @include adaptive(767) {
        top: 15px;
        right: 10px;
      }
    }

    .c-modal {
      &__body {
        padding: 50px;
        @include adaptive(767) {
          padding-left: 15px;
          padding-right: 15px;
          padding-bottom: 30px;
        }
      }

      &__title {
        padding-bottom: 30px;
        font-size: 26px;
        font-weight: bold;
        text-align: center;
        color: #091e42;
      }

      &__form {
        display: flex;
        flex-direction: column;
        width: 100%;

        div {
          display: flex;
          justify-content: center;
          width: 100%;
          padding-bottom: 15px;

          &:last-of-type {
            padding-top: 25px;
          }

          input {
            outline: none;
            width: 100%;
            height: 45px;
            padding: 0 20px;
            border-radius: 3px;
            box-shadow: none;
            border: 2px solid transparent;
            background-color: rgba(#c5c5ff, 0.4);
            letter-spacing: 0.5px;
            font-size: 13px;
            font-weight: 500;
            @include inputPlaceholder {
              font-size: 13px;
              font-weight: 500;
              color: #505f79;
            }
          }

          button {
            white-space: nowrap;
            height: 45px;
            font-size: 13px;
            font-weight: 500;
            letter-spacing: 0.5px;
          }
        }
      }
    }
  }
}

// MODAL :: END